.FormTitle {
  color: #707c8b;
  font-weight: 300;
  margin-bottom: 30px;
}

.FormTitle__Link {
  text-decoration: none;
  display: inline-block;
  font-size: 1.7em;
  margin: 0 10px;
  color: #707c8b;
  padding-bottom: 5px;
}

.FormTitle__Link--Active {
  color: #707c8b;
  border-bottom: 1px solid #199087;
